import React, { useContext, useState, useEffect } from 'react';

import { Link } from 'react-router-dom';
import axios from 'axios';
import history from '../utils/history';
import {SiteContext } from '../utils/context';

import { Button } from 'react-bootstrap';
//import MaroonCard from '../components/MaroonCard';
//import { BoostrapInput  } from '../utils/html5validation';




const ShowPost = (props: any) => {
  const context = useContext(SiteContext)

  const [stateLocal, setState] = useState<{
    comment: '',
    fetched: boolean,
    cid: number,
    delete_comment_id: number,
    edit_comment_id: number,
    edit_comment: string,
    comments_arr: [] | any,
    cur_user_id: any,
    like_post: boolean,
    likes: number,
    like_user_ids: [],
    post_title: string | any,
    post_body: string | any,
    post_author: string | any,
    post_id: string | any
  }>({
    comment: '',
    fetched: false,
    cid: 0,
    delete_comment_id: 0,
    edit_comment_id: 0,
    edit_comment: '',
    comments_arr: null,
    cur_user_id: null,
    like_post: true,
    likes: 0,
    like_user_ids: [],
    post_title: null,
    post_body: null,
    post_author: null,
    post_id: null
  })



  useEffect(() => {
    if (props.location.state && !stateLocal.fetched) {
      console.log('tttt')
      setState({
        ...stateLocal,
        fetched: true,
        likes: props.location.state.post.post.likes,
        like_user_ids: props.location.state.post.post.like_user_id,
        post_title: props.location.state.post.post.title,
        post_body: props.location.state.post.post.body,
        post_author: props.location.state.post.post.author,
        post_id: props.location.state.post.post.pid
      })
    }
  }, [stateLocal,
    props.location])

  useEffect(() => {
    if (!props.location.state && !stateLocal.fetched) {
      console.log('yyyy')
      const post_id = props.location.pathname.substring(6)

      axios.get('/api/get/post',
        { params: { post_id: post_id } })
        .then(res => res.data.length !== 0
          ? setState({
            ...stateLocal,
            fetched: true,
            likes: res.data[0].likes,
            like_user_ids: res.data[0].like_user_id,
            post_title: res.data[0].title,
            post_body: res.data[0].body,
            post_author: res.data[0].author,
            post_id: res.data[0].pid
          })
          : null
        )
        .catch((err) => console.log(err))
    }
  }, [stateLocal,
    props.location])

  useEffect(() => {
    if (!stateLocal.comments_arr) {
      if (props.location.state) {
        const post_id = props.location.pathname.substring(6)
        axios.get('/api/get/allpostcomments',
          { params: { post_id: post_id } })
          .then(res => res.data.length !== 0
            ? setState({ ...stateLocal, comments_arr: [...res.data] })
            : null)
          .catch((err) => console.log(err))
      }
    }
  }, [props.location, stateLocal])


  const handleCommentSubmit = (submitted_comment: any) => {
    if (stateLocal.comments_arr) {
      setState({
        ...stateLocal, comments_arr: [submitted_comment,
          ...stateLocal.comments_arr]
      })
    } else {
      setState({ ...stateLocal, comments_arr: [submitted_comment] })
    }
  };

  const handleCommentUpdate = (comment: any) => {
    const commentIndex = stateLocal.comments_arr.findIndex((com: any) => com.cid === comment.cid)
    var newArr = [...stateLocal.comments_arr]
    newArr[commentIndex] = comment

    setTimeout(() => setState({ ...stateLocal, comments_arr: [...newArr], edit_comment_id: 0 }), 100)
  };


  const handleCommentDelete = (cid: any) => {
    setState({ ...stateLocal, delete_comment_id: cid })
    const newArr = stateLocal.comments_arr.filter((com: any) => com.cid !== cid)
    setState({ ...stateLocal, comments_arr: newArr })
  };

  const handleEditFormClose = () => {
    setState({ ...stateLocal, edit_comment_id: 0 })
  }


  const RenderComments = (props: any) => {
    return (
      <div className={stateLocal.delete_comment_id === props.comment.cid
        ? "FadeOutComment"
        : "CommentStyles"}>
        <div>
          <p>{props.comment.comment} </p>
          <small>
            {props.comment.date_created === 'Just Now'
              ? <div> {props.comment.isEdited
                ? <span> Edited </span>
                : <span> Just Now </span>}</div>
              : props.comment.date_created
            }
          </small>
          <p> By: {props.comment.author} </p>
        </div>
        <div>
          {props.cur_user_id === props.comment.user_id
            ? !props.isEditing
              ? <div>
                <button onClick={() => setState({
                  ...stateLocal,
                  edit_comment_id: props.comment.cid,
                  edit_comment: props.comment.comment
                })
                }>
                  Edit
                </button>
              </div>
              : <form onSubmit={(event: any) => handleUpdate(event, props.comment.cid)}>
                <input
                  aria-label='jhjk'
                  autoFocus={true}
                  name="edit_comment"
                  id="editted_comment"
                  // label="Comment"
                  value={stateLocal.edit_comment}
                  onChange={handleEditCommentChange}
                />
                <br />
                <Button type='submit'>
                  Agree
                </Button>
                <Button type="button" onClick={handleEditFormClose}>
                  Cancel
                </Button>
                <Button onClick={() => handleDeleteComment(props.comment.cid)}>
                  Delete
                </Button>
              </form>
            : null}
        </div>
      </div>
    );
  }



  const handleEditCommentChange = (event: any) => (
    setState({
      ...stateLocal,
      edit_comment: event.target.value
    })
  );


  const handleSubmit = (event: any) => {
    event.preventDefault()
    setState({ ...stateLocal, comment: '' })

    const comment = event.target.comment.value
    const user_id = context.authStates!.profile!._id
    const username = context.authStates!.profile!.username
    const post_id = stateLocal.post_id
    const current_time = "Just Now"
    const temp_cid = Math.floor(Math.random() * 1000);

    const submitted_comment = {
      cid: temp_cid,
      comment: comment,
      user_id: user_id,
      author: username,
      date_created: current_time
    }

    const data = {
      comment: event.target.comment.value,
      post_id: post_id,
      user_id: user_id,
      username: username
    }

    axios.post('/api/post/commenttodb', data)
      .then(res => console.log(res))
      .catch((err) => console.log(err))
    window.scroll({ top: 0, left: 0, behavior: 'smooth' })
    handleCommentSubmit(submitted_comment)
  }

  const handleUpdate = (event: any, cid: any) => {
    event.preventDefault()
    console.log(event)
    console.log(cid)
    const comment = event.target.editted_comment.value
    const comment_id = cid
    const post_id = stateLocal.post_id
    const user_id = context.authStates!.profile!._id
    const username = context.authStates!.profile!.username
    const isEdited = true
    const current_time = "Just Now"

    const edited_comment = {
      cid: comment_id,
      comment: comment,
      user_id: user_id,
      author: username,
      date_created: current_time,
      isEdited: isEdited
    }

    const data = {
      cid: comment_id,
      comment: comment,
      post_id: post_id,
      user_id: user_id,
      username: username
    }

    axios.put('/api/put/commenttodb', data)
      .then(res => console.log(res))
      .catch((err) => console.log(err))
    handleCommentUpdate(edited_comment);
  }

  const handleDeleteComment = (cid: any) => {
    const comment_id = cid
    console.log(cid)
    axios.delete('/api/delete/comment', { data: { comment_id: comment_id } })
      .then(res => console.log(res))
      .catch((err) => console.log(err))
    handleCommentDelete(cid)
  }

  const handleLikes = () => {
    const user_id: never = context.authStates!.profile!._id as never;
    const post_id = stateLocal.post_id

    const data = { uid: user_id, post_id: post_id }
    console.log(data)
    if (!stateLocal.like_user_ids.includes(user_id)) {
      axios.put('/api/put/likes', data)
        .then(() => !stateLocal.like_user_ids.includes(user_id)
          && stateLocal.like_post
          ? setState({
            ...stateLocal,
            likes: stateLocal.likes + 1,
            like_post: false
          })
          : null)
        .catch(err => console.log(err))
    };
  }


  return (
    <div>
      <div>
        <h2>Post</h2>
        {stateLocal.comments_arr || props.location.state
          ? <div>
            <p>{stateLocal.post_title}</p>
            <p>{stateLocal.post_body}</p>
            <p>{stateLocal.post_author}</p>
          </div>
          : null
        }

        <div style={{ cursor: 'pointer' }} onClick={context.authStates?.isAuthenticated
          ? () => handleLikes()
          : () => history.replace('/signup')}>
          <i className="material-icons">thumb_up</i>
          <small className="notification-num-showpost">
            {stateLocal.likes}
          </small>
        </div>
      </div>
      <div>

        <h2> Comments:</h2>
        {stateLocal.comments_arr
          ? stateLocal.comments_arr.map((comment: any) =>
            <RenderComments comment={comment}
              cur_user_id={context.authStates!.profile!
                ? context.authStates!.profile!._id
                : null}
              key={comment.cid}
              isEditing={comment.cid === stateLocal.edit_comment_id
                ? true
                : false}
            />)
          : null
        }
      </div>
      <div>
        <form onSubmit={handleSubmit}>
          <input
            aria-label='Comment'
            type="text"
            id="comment"
          //label="Comment"

          />
          <br />
          <div>

          </div>
          {context.authStates?.isAuthenticated
            ? <Button variant="contained" color="primary" type="submit">
              Submit
            </Button>
            : <Link to="/signup">
              <Button variant="contained" color="primary">
                Signup to Comment
              </Button>
            </Link>
          }
        </form>
      </div>
      <div>
      </div>
    </div>
  )
}




export default ShowPost;

