import React from 'react';
import { GenericInput,Polymorphic,TextProps} from '../components/Polymorphic'
import {getRandom, checkedUp, uuid, ProcessChanges, useProcessForm} from './html5validation'


export interface FormProps{
        attrs?:React.FormHTMLAttributes<HTMLFormElement>,
        cleandata?: Function,
        children?: React.ReactNode;
}    

interface FormBuilderElementProps {
    lable: string
    element: TextProps<any>,
    selectOptions?: SelectOptionsProps
}

interface FormBuilderProps {
    isfloating:boolean,
    elements: FormBuilderElementProps[],
    form: FormProps,
    addSubmit?: boolean,
    extra?: JSX.Element
}

interface SelectOptionsProps {
    lable: string,
    options: React.DetailedHTMLProps<React.OptionHTMLAttributes<HTMLOptionElement>, HTMLOptionElement>[],
}


function GenericForm(props: FormProps) {
    const { handleSubmit, validated, processChanges } = useProcessForm();

    return (
        <form {...props.attrs} className={validated ? 'was-validated' : 'needs-validation'} onChange={processChanges} noValidate  onSubmit={(e: any) => handleSubmit(e, props.cleandata!)}>
            {props.children}
        </form>
    )
}

function FormBuilder(props: FormBuilderProps) {

    let _elements = props.elements.map((item: FormBuilderElementProps) => {
        let _options = null;
        if (item.selectOptions) {
            _options = <><option key={getRandom()} value="" >Select menu</option>
                {item.selectOptions.options.map(o => <option key={getRandom()} {...o}> {o.title} </option>)}
            </>
        }
        if (item.element) {

            return <GenericInput  isfloating={props.isfloating.toString()} lable={item.lable}  {...item.element}  >
                {_options}
            </GenericInput>;
        }
        else {
            throw new Error('empty element no input or select data form formBuilder :' + item.lable)
        }
    })
    return (<GenericForm {...props.form}> {_elements} {props.addSubmit ? <button type='submit' className='btn btn-primary'> submit </button> : ""}{props.extra} </GenericForm>);
}

export { FormBuilder, getRandom, GenericForm ,GenericInput, checkedUp, ProcessChanges, useProcessForm, uuid, Polymorphic}