import React, { useContext} from 'react';
import { Route, BrowserRouter, Routes, Navigate } from 'react-router-dom';
//import history from './utils/history';

import {SiteContext} from './utils/context';
import AuthCheck from './utils/authcheck';
import Home from './pages/Home';
import { ForgotPassword } from './pages/ForgotPassword';
import Login from './pages/Login';
import { NotFound } from './pages/NotFound';
import { Lock } from './pages/Lock';
import { ServerError } from './pages/ServerError';
import Signup from './pages/Signup';
import UserProfile from './pages/UserProfile'
import Blog from './pages/blog'
import Contactme from './pages/contact'
import Portfolio from './pages/portfolio'
import Layout from './components/Layout';
//========== blog routes
//import Home from './hooks/home';
//import Header from './hooks/header';
import HooksContainer1 from './hooks/hook1';
//import Callback from './hooks/callback';
import HooksForm from './hooks/hooks_form1';
import PrivateComponent from './hooks/privatecomponent';
import SignUp from './hooks/signup';


import Profile from './Profile/profile';
import ShowUser from './Profile/showuser';
import SendMessage from './Profile/sendmessage';
import ShowMessages from './Profile/showmessages';
import ReplytoMessage from './Profile/replytomessage';

import Posts from './Blog/posts';
import AddPost from './Blog/addpost';
import ShowPost from './Blog/showpost';
import EditPost from './Blog/editpost';

import Test from './pages/test';


const PrivateRoute = (props: React.PropsWithChildren<{ auth: boolean }>) => (<> {props.auth ? props.children : <Navigate to='/signup' />} </>)

const AppRoutes = () => {
  const context = useContext(SiteContext)

  return (
      <BrowserRouter>
        {/*<Header />*/}
        <Routes>
          <Route path='/' element={<Layout />}>
            <Route index element={<Home />} />
            <Route path='test' element={<Test />} />
            <Route path='portfolio' element={<Portfolio />} />
            <Route path='forgetPassword' element={<ForgotPassword />} />
            <Route path='notfound' element={<NotFound />} />
            <Route path='servererror' element={<ServerError />} />
            <Route path='lock' element={<Lock />} />
            <Route path='profile' element={<UserProfile />} />
            <Route path='login' element={<Login />} />
            <Route path='signup' element={<Signup />} />
            <Route path='hireme' element={<Contactme />} />
            <Route path='contact' element={<Contactme />} />
            <Route path='*' element={<NotFound />} />


            <Route path='hooksform' element={<HooksForm />} />
            <Route path='hookscontainer' element={<HooksContainer1 />} />
            <Route path='authcheck' element={<AuthCheck />} />

            <Route path='signup' element={<SignUp />} />
            <Route path="user/:name" element={<ShowUser />} />

            <Route path='profile' element={<Profile />} />
            <Route path='profile' element={<PrivateRoute auth={context.authStates?.isAuthenticated!}> <Profile /> </PrivateRoute>} />

            <Route path='blog' element={<Blog />}></Route>
            <Route path='blog/posts' element={<Posts />} />
            <Route path='blog/post/:pid' element={<ShowPost />} />
            <Route path='blog/editpost/:pid' element={<EditPost />} />
            <Route path='blog/addpost' element={<AddPost />} />
            
            <Route path="/sendmessage" element={<PrivateRoute auth={context.authStates?.isAuthenticated!}> <SendMessage />  </PrivateRoute>} />
            <Route path="/showmessages/:id" element={<PrivateRoute auth={context.authStates?.isAuthenticated!}> <ShowMessages /> </PrivateRoute>} />
            <Route path="/replytomessage" element={<PrivateRoute auth={context.authStates?.isAuthenticated!}> <ReplytoMessage /> </PrivateRoute>} />

            <Route path='/privateroute' element={<PrivateRoute auth={context.authStates?.isAuthenticated!}> <PrivateComponent /> </PrivateRoute>} />
            {/*<Route path='/callback' render={(props) => { context.handleAuth(props); return <Callback />}} />*/}
          </Route>
        </Routes>

      </BrowserRouter>
  )
}



export default AppRoutes;
