import * as ACTION_TYPES from '../actions/action_types'
import {PostActionType, IPost} from '../actions/actions';

export const initialState:IPost[] | []= [];

export const PostsReducer = (state = initialState, action:PostActionType) => {
    switch(action.type) {
      case ACTION_TYPES.FETCH_DB_POSTS:
        return {
          ...state,
          ...action.payload
        }
      case ACTION_TYPES.REMOVE_DB_POSTS:
        return {
          ...state,
          posts: []
        }
    
      default:
        return state
    }
}
