import * as ACTION_TYPES from './action_types'

export interface IAuthPayload{
  isAuthenticated: boolean,
  profile: IProfile | null,
  tokens:ITokens | null,
}
export interface ITokens {
  accessToken?: string;
  accessToken_expireAt?: number;
}
export interface IProfile {
  _id?: string
  email?: string,
  email_verified?:boolean
  username?: string,
  firstname?: string,
  lastname?: string,
  active?:boolean,
  roles?:{_id:string,name:string}[],
  refreshToken?:string,
  refreshToken_expireAt?:Date
}

export interface IPost{
  title?:string
  image?: string,
  descriptions?:string,
  body?: string,
  featured?:boolean,
  category?:{_id:string,name:string}[],
  author?: IProfile | string,
  comments?: {_id:string,comment:IComment}[],
  likes?: number
  like_user_id?: {_id:string,comment:any}[],
}
export interface IComment{
  _id:string,
  body:string,
  author: IProfile | string,
  post_id:string
}

export interface AuthActionType{
  type:string;
  payload?: IAuthPayload
}
export interface PostActionType{
  type:string;
  payload?: IPost[] 
}
export interface ActionType{
  type:string;
  payload?: any
}
export const login_success = (payload:IAuthPayload):AuthActionType => {
  let  s_payload = JSON.stringify(payload);
  sessionStorage.setItem("auth",s_payload);
  return {
    type: ACTION_TYPES.LOGIN_SUCCESS,
    payload:  JSON.parse(s_payload)
  }
}

export const login_failure = ():AuthActionType => {
  return {
    type: ACTION_TYPES.LOGIN_FAILURE,
    payload:{
      isAuthenticated : false,
      tokens:null,
      profile:null
    }
  }
}
export const logout = ():AuthActionType => {
  return {
    type: ACTION_TYPES.LOGOUT,
    payload:{
      isAuthenticated : false,
      tokens:null,
      profile:null
    }
  }
}
export const SUCCESS = {
  type: ACTION_TYPES.SUCCESS
}

export const FAILURE = {
  type: ACTION_TYPES.FAILURE
}

export const success = () => {
  return {
    type: ACTION_TYPES.SUCCESS
  }
}

export const failure = () => {
  return {
    type: ACTION_TYPES.FAILURE
  }
}

export const user_input_change = (text:any) => {
  return {
    type: ACTION_TYPES.USER_INPUT_CHANGE,
    payload: text
  }
}

export const user_input_submit = (text:any) => {
  return {
    type: ACTION_TYPES.USER_INPUT_SUBMIT,
    payload: text
  }
}


export const set_db_posts = (posts:IPost[]) => {
  return {
    type: ACTION_TYPES.FETCH_DB_POSTS,
    payload: posts
  }
}

export const remove_db_posts = () => {
  return {
    type: ACTION_TYPES.REMOVE_DB_POSTS
  }
}
