import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import moment from 'moment';
import {SiteContext } from '../utils/context';

import { Card,Nav, Button} from 'react-bootstrap';
import MaroonCard from '../components/MaroonCard';
import { BoostrapInput  } from '../utils/boostrapForms';

import '../App.css';
import '../styles/pagination.css';


const Posts = (props:any) => {
const context = useContext(SiteContext)!;


const [stateLocal, setState] = useState<{ posts: any[],
                                          fetched: boolean,
                                          first_page_load: boolean,
                                          pages_slice: number[],
                                          max_page: number,
                                          items_per_page: number,
                                          opacity:number,
                                          currentPage: number,
                                          num_posts: number,
                                          posts_slice: any,
                                          posts_search: any[],
                                          posts_per_page: number
                                      }>({
                                        posts: [],
                                        fetched: false,
                                        first_page_load: false,
                                        pages_slice: [1, 2, 3, 4, 5],
                                        max_page: 1,
                                        items_per_page: 3,
                                        opacity:1,
                                        currentPage: 1,
                                        num_posts: 1,
                                        posts_slice: null,
                                        posts_search: [],
                                        posts_per_page: 3}
                                        )


  useEffect(() => {
   if(!context.postsState) {
     
      axios.get(context.ajax!.get!('posts'))
        .then(res => context.handleAddPosts!(res.data) )
        .catch((err) => console.log(err))
      

      }
      
    if (context.postsState && !stateLocal.fetched) {
      const indexOfLastPost = 1 * stateLocal.posts_per_page
      const indexOfFirstPost = indexOfLastPost - stateLocal.posts_per_page
      const last_page = Math.ceil(context.postsState.length/stateLocal.posts_per_page)

      setState({...stateLocal,
                fetched: true,
                posts: [...context.postsState],
                num_posts: context.postsState.length,
                max_page: last_page,
                posts_slice: context.postsState.slice(indexOfFirstPost,
                                                      indexOfLastPost)
                })
      }
    }, [context, stateLocal])


  useEffect(() => {
    let page = stateLocal.currentPage
    let indexOfLastPost = page * 3;
    let indexOfFirstPost = indexOfLastPost - 3;

    setState({...stateLocal,
              posts_slice: stateLocal.posts.slice(indexOfFirstPost,
                                                  indexOfLastPost) })
  }, [stateLocal.currentPage]) //eslint-disable-line




 const add_search_posts_to_state = (posts:any) => {
    setState({...stateLocal, posts_search: []});
    setState({...stateLocal, posts_search: [...posts]});
 }


const handleSearch = (event:any) => {
   setState({...stateLocal, posts_search: []});
   const search_query = event.target.value
  context.ajax!.get!('posts/', {search_query: search_query})
     .then((res:any) => res.data.length !== 0
                    ? add_search_posts_to_state(res.data)
                    : null )
     .catch(function (error:any) {
       console.log(error);
       })
   }

const RenderPosts = (post:any) => (
  <Card>
    <Card.Header> 
      <Card.Title>
      <Link className='card-link' to={'/posts/' + post.post.pid} state={post}> {post.post.title}
        </Link> 
            
      </Card.Title>
      
          <Card.Subtitle className="FlexColumn">
            <div className="FlexRow">
            {  moment(post.post.date_created).format('MMMM Do, YYYY | h:mm a') }
            </div>
            <div className="FlexRow">
              By:
              <Link style={{paddingLeft: '5px', textDecoration: 'none'}} to={"/user/" + post.post.author} state={post}>
               { post.post.author }
               </Link>
             </div>
             <div className="FlexRow">
              <i className="material-icons">thumb_up</i>
              <div className="notification-num-allposts"> {post.post.likes} </div>
            </div>
          </Card.Subtitle>
          
        </Card.Header>
          <div/>
 
    <Card.Body>
      <Card.Text style={{overflow: 'hidden' }}> {post.post.body} </Card.Text>
    </Card.Body>
  
  </Card>
)

const page_change = (page:any) => {
  window.scrollTo({top:0, left: 0, behavior: 'smooth'})

  //variables for page change
  let next_page = page + 1
  let prev_page = page - 1

  //handles general page change
  //if(state.max_page < 5 return null)
  if(page > 2 && page < stateLocal.max_page - 1) {
    setState({...stateLocal,
              currentPage: page,
              pages_slice: [prev_page - 1,
                            prev_page,
                            page,
                            next_page,
                            next_page + 1],
            })
   }
   if(page === 2 ) {
     setState({...stateLocal,
              currentPage: page,
               pages_slice: [prev_page,
                             page,
                             next_page,
                             next_page + 1,
                             next_page + 2],
             })
    }
   //handles use case for user to go back to first page from another page
   if(page === 1) {
     setState({...stateLocal,
              currentPage: page,
               pages_slice: [page,
                             next_page,
                             next_page + 1,
                             next_page + 2,
                             next_page + 3],
          })
   }
   //handles last page change
   if(page === stateLocal.max_page) {
     setState({...stateLocal,
               currentPage: page,
               pages_slice: [prev_page - 3,
                             prev_page - 2,
                             prev_page - 1,
                             prev_page,
                             page],
             })
   }
   if(page === stateLocal.max_page - 1) {
     setState({...stateLocal,
               currentPage: page,
               pages_slice: [prev_page - 2,
                             prev_page - 1,
                             prev_page,
                             page,
                             next_page],
             })
   }
 }


return(
    <MaroonCard title='Posts'>
    <Nav style={{opacity: stateLocal.opacity, transition: 'opacity 2s ease'}}>
    { context.authStates?.isAuthenticated
      ?  <Link to="/addpost">
            <Button variant="info" color="primary">
              Add Post
            </Button>
          </Link>
      : <Link to="/signup">
            <Button variant="danger" color="primary">
              Sign Up to Add Post
            </Button>
          </Link>
        }
    </Nav>
    <br />

    <BoostrapInput InputGroup={{ className: "mb-3" , itemProp:"bi bi-search"}} FormControl={{type:"search",title:"search", required:true, minLength:3, maxLength:50,onChange:handleSearch }}/>
              
    <hr />
     <Button  onClick={() => console.log(stateLocal)}> Button </Button>
     <Button onClick={() => console.log(context)}> Button </Button>
     <br />
     <div>
       {stateLocal.posts_search
         ? stateLocal.posts_search.map(post =>
           <RenderPosts key={post.pid + 1000} post={post} />
          )
          : null
        }
      </div>

    <h1>Posts</h1>
      <div>
        {stateLocal.posts_slice
          ? stateLocal.posts_slice.map((post:any) =>
            <RenderPosts key={post.pid} post={post} />
           )
          : null
         }
      </div>
      <div>
          <div className="FlexRow">
              <Button onClick={() => page_change(1) }> First </Button>
              <Button onClick={() => page_change(stateLocal.currentPage - 1) }> Prev </Button>
                 {stateLocal.pages_slice.map((page) =>
                     <div
                       onClick={() => page_change(page)}
                       className={stateLocal.currentPage === page
                                     ? "pagination-active"
                                     : "pagination-item" }
                       key={page}>
                         {page}
                      </div>
                 )}
               <Button onClick={() => page_change(stateLocal.currentPage + 1)}> Next </Button>
               <Button onClick={() => page_change(stateLocal.max_page)}> Last </Button>
             </div>
       </div>
    </MaroonCard>
)}

export default Posts;
