import * as react from 'react';
import { Card} from 'react-bootstrap';

interface MaroonCardProps {
    title?:string
}
export default function MaroonCard(props:react.PropsWithChildren<MaroonCardProps>){

   return(
    <Card className='card shadow-none card-body card-maroon card-outline py-1'>
        {
            props.title ? 
    <div className="ribbon-wrapper ribbon-xl">
                <div className="ribbon bg-maroon text-lg">
                {props.title}
                </div>
            </div>
        : ""
}
      {props.children}

    </Card>
   )
}