import React, { useReducer } from 'react';
import history from './history';
//import Context from './utils/context';
import * as ACTIONS from '../store/actions/actions';

import * as Reducer1 from '../store/reducers/plain_reducer';
import * as AuthReducer from '../store/reducers/auth_reducer';
import * as FormReducer from '../store/reducers/form_reducer';
import * as PostsReducer from '../store/reducers/posts_reducer';
import AppRoutes from '../routes';


export interface ISiteContextProps {
  baseUrl?: string,
  stateProp1?: boolean;
  dispatchContextTrue?: Function;
  dispatchContextFalse?: Function;

  //Auth Reducer
  //keep for global state
  authStates?: ACTIONS.IAuthPayload;

  handleUserLogin?(payload:ACTIONS.IAuthPayload): void;
  handleUserLoginFailur?(): void;
  handleUserLogout?(): void;

  //Form Reducer
  useContextChangeState?: any;
  useContextSubmitState?: any;
  useContextSubmit?(event: any): any;
  useContextChange?(event: any): any;

  //Posts State
  postsState?: ACTIONS.IPost[];
  handleAddPosts?(posts: ACTIONS.IPost[]): any;
  handleRemovePosts?(): any;
  ajax?: IAjax,
  //Handle auth  
  auth?: IAuth
}

export interface IAjax {
  setUrl(path?: string, querys?: Record<string, any>): URL;
  init(method: string, url: URL, data?: any): any;
  get(path?: string, querys?: Record<string, any>): any;
  post(path: string, data: any, querys?: Record<string, any>): any;
  put(path: string, data: any, querys?: Record<string, any>): any;
  delete(path: string, data: any, querys?: Record<string, any>): any;

}

export interface ProcessTokens {
  getAccessToken(): ACTIONS.ITokens | null
  getItem(name: string): any;
  saveItem(name: string, item: any): void;
  clearItems(): void;
  isAuthenticated(): boolean;
}

export interface IAuth extends ProcessTokens {
  clearItems(): void;
  setProfile(email: string): Promise<any>
  login(email: string, password: string): any;
  logout(): void;
}

export const SiteContext = React.createContext<ISiteContextProps>({})

export const ContextState = (props: ISiteContextProps) => {

  // Plain Reducer
  const [stateReducer1, dispatchReducer1] = useReducer(Reducer1.Reducer1, Reducer1.initialState)
  // Auth Reducer
  const [stateAuthReducer, dispatchAuthReducer] = useReducer(AuthReducer.AuthReducer, AuthReducer.initialState)
  //  Form Reducer
  const [stateFormReducer, dispatchFormReducer] = useReducer(FormReducer.FormReducer, FormReducer.initialState)
  //  Posts Reducer
  const [statePostsReducer, dispatchPostsReducer] = useReducer(PostsReducer.PostsReducer, PostsReducer.initialState)

  var values = {
    baseUrl: 'http://localhost:8000',
    stateProp1: stateReducer1.stateprop1,
    dispatchContextTrue: () => dispatchReducer1(ACTIONS.success()),
    dispatchContextFalse: () => dispatchReducer1(ACTIONS.failure()),

    //Auth Reducer
    authStates: stateAuthReducer,
    handleUserLogin: (payload:ACTIONS.IAuthPayload) => dispatchAuthReducer(ACTIONS.login_success(payload)),
    handleUserLoginFailur: () => dispatchAuthReducer(ACTIONS.login_failure()),
    handleUserLogout: () => dispatchAuthReducer(ACTIONS.logout()),
   //Posts State
    postsState: statePostsReducer,
    handleAddPosts: (posts: ACTIONS.IPost[]) => dispatchPostsReducer(ACTIONS.set_db_posts(posts)),
    handleRemovePosts: () => dispatchPostsReducer(ACTIONS.remove_db_posts()),

    //Form Reducer
    useContextChangeState: stateFormReducer.user_textChange,
    useContextSubmitState: stateFormReducer.user_textSubmit,
    useContextSubmit: (event: any) => {
      event.preventDefault();
      event.persist();
      dispatchFormReducer(ACTIONS.user_input_submit(event.target.useContext.value))
    },
    useContextChange: (event: any) => dispatchFormReducer(ACTIONS.user_input_change(event.target.value)),


    ajax: ((): IAjax => {
      let headers: Record<string, string> = {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': 'http://localhost:8000',
      }
      if (stateAuthReducer.isAuthenticated && stateAuthReducer.tokens) {
        headers['Authorization'] = `Bearer ${stateAuthReducer.tokens.accessToken}`
      }
      let ajaxObj = {

        setUrl: (path?: string, querys?: Record<string, any>): URL => {
          let url = new URL(path!, 'http://localhost:8000');
          if (querys)
            for (let k in querys) {
              url.searchParams.append(k, querys[k]);
            }
          return url;
        },
        init: async (method: string, url?: URL, data?: any) => {

          const body = JSON.stringify(data) // body data type must match "Content-Type" header
          const response = await fetch(url?.href!, {
            method: method, // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, same-origin
            //credentials: 'same-origin', // include, *same-origin, omit
            headers: headers,
            // redirect: 'follow', // manual, *follow, error
            referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
            body
          });

          if (response.ok)
            return await response.json();
          else
            return response.ok
          // parses JSON response into native JavaScript objects
        },

        get: async (path?: string, querys?: Record<string, any>) =>  await ajaxObj.init('GET', ajaxObj.setUrl(path!, querys))
        ,
        post: async (path: string, data: any, querys?: Record<string, any>) =>  await ajaxObj.init('POST', ajaxObj.setUrl(path!, querys!), data)
        ,
        put: async (path: string, data: any, querys?: Record<string, any>) =>  await ajaxObj.init('PUT', ajaxObj.setUrl(path!, querys!), data)
        ,
        delete: async (path: string, data: any, querys?: Record<string, any>) =>  await ajaxObj.init('PUT', ajaxObj.setUrl(path!, querys!), data)
      }

      return ajaxObj;

    })(),
    auth: ((): IAuth => {
      let obj = {
        getAccessToken: (): ACTIONS.ITokens | null => {
          return stateAuthReducer.isAuthenticated ? obj.getItem('auth')['tokens'] : null;
        },
        getItem: (name: string) => {
          if (name) {
            let item = sessionStorage.getItem(name);
            return item ? JSON.parse(item) : null;
          } else {
            return null;
          }
        },
        saveItem: (name: string, item: any) => {
          item = JSON.stringify(item)
          if (item) {
            sessionStorage.setItem(name, item);
          }
        },
        clearItems: () => {
          sessionStorage.clear()
        },
        isAuthenticated: () => {
          let tokens = obj.getAccessToken();

          return tokens && tokens.accessToken_expireAt ? new Date().getTime() < tokens!.accessToken_expireAt : false;
        },
        setProfile: async (email: string): Promise<any> => {
          try {
            if (email) {
              let responce: any = await values.ajax.get('auth/profile/', { email: email! });
              //console.log(responce);

              if (responce && responce.success) {
                obj.saveItem('profile',JSON.stringify(responce as string))
                return true;
              }
              return false;
            } else {
              return false;
            }
          } catch (err: any) {
            console.log(err);
            throw err;
          };
        },
        login: async (email: string, password: string) => {
          try {

            let data = await values.ajax.post('auth/signin', { email, password, username: 'tuban' });
       
            if (data && data.user) {
              values.handleUserLogin!({
                isAuthenticated:true,
                tokens:data.tokens,
                profile:data.user
              });
              return true;
            } else {
              values.handleUserLoginFailur();
              return data.error ? data : false;
            }

          } catch (err) {
            values.handleUserLoginFailur();
            console.log(err);
            throw err;
          };
        },

        logout: () => {
          obj.clearItems();
          values.handleUserLogout!()
          history.replace('/')

        }
      }
      return obj;
    })(),
  }

  return (
    <SiteContext.Provider
      value={values}>
      <AppRoutes />
    </SiteContext.Provider>
  )
}

